import React, { useEffect } from "react";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import Header from "./components/Header";
import Footer from "./components/Footer";
import Home from "./components/Home";
import XGPlan from "./components/XGPlan";
import Pool from "./components/Pool";
import Faq from "./components/Faq";
import Roadmap from "./components/Roadmap";
import StageNovice from "./components/StageNovice";
import StageInitiate from "./components/StageInitiate";
import StageApprentice from "./components/StageApprentice";
import StageAdept from "./components/StageAdept";
import StageExpert from "./components/StageExpert";
import StageMaster from "./components/StageMaster";
import StageGrandmaster from "./components/StageGrandmaster";
import StageFree from "./components/StageFree";
import UserProfile from "./components/UserProfile";
import TransactionsList from "./components/TransactionsList";
import MessagesList from "./components/MessagesList";
import StickyMenu from "./components/StickyMenu";

// Load Telegram Web App script
const loadTelegramScript = () => {
  const script = document.createElement("script");
  script.src = "https://telegram.org/js/telegram-web-app.js";
  script.async = true;
  document.body.appendChild(script);
};

function App() {
  useEffect(() => {
    loadTelegramScript();

    // Expand WebApp to full screen once the script is loaded
    const handleTelegramReady = () => {
      if (window.Telegram.WebApp) {
        window.Telegram.WebApp.expand();
        window.Telegram.WebApp.onEvent("viewportChanged", function () {
          if (!window.Telegram.WebApp.isExpanded) {
            window.Telegram.WebApp.expand();
          }
        });
      }
    };

    window.addEventListener("load", handleTelegramReady);

    return () => {
      window.removeEventListener("load", handleTelegramReady);
    };
  }, []);

  return (
    <div className="App">
      <Header />
      <StickyMenu />
      <main id="main-content">
        <Router>
          <Routes>
            <Route path="/my-twa/stages/novice" element={<StageNovice />} />
            <Route path="/my-twa/stages/initiate" element={<StageInitiate />} />
            <Route
              path="/my-twa/stages/apprentice"
              element={<StageApprentice />}
            />
            <Route path="/my-twa/stages/adept" element={<StageAdept />} />
            <Route path="/my-twa/stages/expert" element={<StageExpert />} />
            <Route path="/my-twa/stages/master" element={<StageMaster />} />
            <Route
              path="/my-twa/stages/grandmaster"
              element={<StageGrandmaster />}
            />
            <Route path="/my-twa/stages/free" element={<StageFree />} />
            <Route path="/my-twa/faq" element={<Faq />} />
            <Route path="/my-twa/roadmap" element={<Roadmap />} />
            <Route path="/my-twa/xg-plan" element={<XGPlan />} />
            <Route path="/my-twa/pool" element={<Pool />} />
            <Route path="/my-twa/profile" element={<UserProfile />} />
            <Route path="/transactions/list" element={<TransactionsList />} />
            <Route path="/my-twa/messages/list" element={<MessagesList />} />
            <Route path="/my-twa/" element={<Home />} />
          </Routes>
        </Router>
      </main>
      <Footer />
    </div>
  );
}

export default App;
